import React, { useEffect } from 'react'
import { AppContent, AppHeader, AppSidebar } from '../components/index'
import { useDispatch, useSelector } from 'react-redux'
import { getAllItems } from 'src/modules/billing/services/items.service'
import { ErrorBoundary } from 'react-error-boundary'

const DefaultLayout = (props) => {
  const dispatch = useDispatch()
  const showHeader = useSelector((state) => state.app.showHeader)
  useEffect(() => {
    dispatch(getAllItems())
  }, [dispatch])
  return (
    <div>
      <ErrorBoundary fallback={<h1>Algo salió mal!</h1>}>
        <AppSidebar />
        <div className="wrapper d-flex flex-column min-vh-100 bg-light">
          {showHeader && <AppHeader />}
          <div className="body flex-grow-1">
            <AppContent />
          </div>
          {/* <AppFooter /> */}
        </div>
      </ErrorBoundary>
    </div>
  )
}

export default DefaultLayout
